/*
 * Definitions
 */

// Gutter
$gutter: 1.5rem;
$gutter-xlarge: $gutter;
$gutter-large: $gutter;
$gutter-medium: $gutter;
$gutter-small: $gutter;
$gutter-xsmall: $gutter;


/*
 * Gutter
 */

.no-gutter {
	padding: 0 !important;
}

.gutter-top {
	padding-top: $gutter !important;
}

.gutter-bottom {
	padding-bottom: $gutter !important;
}

.gutter-left {
	padding-left: $gutter !important;
}

.gutter-right {
	padding-right: $gutter !important;
}

@media #{$xlarge} {

	.no-gutter-xlarge {
		padding: 0 !important;
	}
	
	.gutter-top {
		padding-top: $gutter-xlarge !important;
	}
	
	.gutter-bottom {
		padding-bottom: $gutter-xlarge !important;
	}
	
	.gutter-left {
		padding-left: $gutter-xlarge !important;
	}
	
	.gutter-right {
		padding-right: $gutter-xlarge !important;
	}
}

